.history-card {
  background: white;
  padding: 12px;
  margin-bottom: 10px;
  border: 1px solid #e0e0e0;
  border-radius: 8px;
  transition: transform 0.1s ease, box-shadow 0.1s ease;
}

.history-card:hover {
  box-shadow: 0 2px 5px rgba(0,0,0,0.1);
  border-color: #d2e3fc;
}

.history-link {
  text-decoration: none;
  color: #1a73e8;
  font-size: 14px;
  font-weight: 500;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
  line-height: 1.4;
}

.history-meta {
  margin-top: 8px;
  font-size: 11px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.date-text {
  color: #70757a;
}

.domain-pill {
  color: #137333;
  background-color: #e6f4ea;
  padding: 2px 8px;
  border-radius: 12px;
  max-width: 110px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  font-weight: 500;
}